.container {
  margin-left: auto;
  margin-right: auto;

  // align-items: center;
  // text-align: center;

  @media screen and(max-width:319px) {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
  @media screen and (min-width: 320px) {
    width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding-left: 40px;
    padding-right: 40px;
  }

  @media screen and (min-width: 1344px) {
    width: 1344px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

